:root {
  --content-width: 1720px;
  --container-offset: clamp(20px, 6vw, 60px);
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --content-fluid-width: 1920px;
  --container-fluid-offset: 0;
  --container-fluid-width: calc(var(--content-fluid-width) + (var(--container-offset) * 2));
}

:root {
  --primary-font: "FONT_NAME", sans-serif;
  --fs-xl: clamp(36px, 5vw, 64px);
  --fs-lg: clamp(20px, 4vw, 48px);
  --fs-md: clamp(18px, 3vw, 32px);
  --fs-sm: clamp(16px, 3vw, 20px);
  --fs-xs: clamp(14px, 3vw, 16px);
  --fw-h: 900;
  --fw-xb: 800;
  --fw-b: 700;
  --fw-sb: 600;
  --fw-m: 500;
  --fw-r: 400;
  --fw-l: 300;
  --fw-xl: 200;
  --fw-t: 100;
  --base-line-heigt: 1.2;
  --main-title-fs: var(--fs-lg);
  --main-title-fw: var(--fw-b);
  --base-title-fs: var(--fs-md);
  --base-title-fw: var(--fw-m);
  --main-text-fs: calc(var(--fs-md) / .8);
  --base-text-fs: var(--fs-sm);
  --main-title-color: var(--dark);
  --base-title-color: var(--dark);
  --main-text-color: var(--dark);
  --base-text-color: var(--dark);
}

:root {
  --primary: #007bff;
  --secondary: #6c757d;
  --success: #28a745;
  --warning: #ffc107;
  --danger: #dc3545;
  --info: #17a2b8;
  --light: #f8f9fa;
  --dark: #343a40;
  --muted: #6c757d;
  --white: #ffffff;
  --black: #000000;
  --site-bg-color: var(--bg-white);
}

:root {
  --scrollbar-color: var(--site-bg-color);
  --scrollbar-thumb-color: var(--primary);
  --scrollbar-width: 6px;
  --scrollbar-border-radius: 0;
  --scrollbar-thumb-border-radius: 0;
}

:root {
  --base-transition: .3s ease-in;
  --fast-transition: .15s ease-in;
  --slow-transition: .6s ease-in;
}

