*,
*::before,
*::after {
  box-sizing: inherit;
  border-radius: 0;
}

html {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  min-height: 100vh;
  min-height: 100dvh;
  -webkit-text-size-adjust: none;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: var(--primary-font);
  font-weight: var(--fw-r);
  line-height: var(--base-line-heigt);
  overflow-x: hidden !important;
  background-color: var(--site-bg-white);
  color: var(--main-text-color);
}
body.body-locked {
  overflow: hidden;
}

footer {
  margin-top: auto;
}

img {
  height: auto;
  width: 100%;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

a[href="tel:*"] {
  white-space: nowrap;
}

address {
  font-style: normal;
}

.hidden {
  display: none !important;
}

picture {
  display: flex;
}

.main-btn {
  background-color: red;
  color: green;
}

.dev-preview-section {
  padding: 20px 0;
  margin-bottom: clamp(30px, 4vw, 80px);
}